export default {
  data() {
    return {
      address: [],
      addressTit: '收货地址'
    }
  },
  beforeCreate() {
    this.$http.get('/api/address')
      .then((res) => {
        if (res.data.data.length) {
          this.address = res.data.data
        }
      })
      .catch((err) => {
        console.log(err)
      })
  },
  created() {
    if (this.$route.query.tabAddress) {
      this.addressTit = "选择收货地址"
    }
  },
  mounted() {},
  methods: {
    tabAddress(data) {
      if (this.$route.query.tabAddress) {
        this.$store.commit('setAddress', data)
        this.$router.push('/waitPay')
      }
    }
  },
  computed: {},
  components: {}
}




