import Vue from 'vue'
import FastClick from 'fastclick'
import App from './App'
import router from './router'
import store from './store'
import './assets/css/common.css'
import footerBar from '@/components/footer'

import { sync } from 'vuex-router-sync'
sync(store, router)

Vue.component('footer-bar', footerBar)



import WechatPlugin from 'vux/src/plugins/wechat/index.js'

Vue.use(WechatPlugin)

import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Card from 'vux/src/components/card/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import Countup from 'vux/src/components/countup/index.vue'
import Clocker from 'vux/src/components/clocker/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'



Vue.use(AjaxPlugin)

Vue.use(ToastPlugin)
Vue.component('toast', Toast)
Vue.component('card', Card)
Vue.component('actionsheet', Actionsheet)
Vue.directive('transfer-dom', TransferDom)
Vue.component('countup', Countup)
Vue.component('clocker', Clocker)
Vue.component('loading', Loading)
Vue.component('x-header', XHeader)
Vue.component('x-button', XButton)
Vue.component('cell-box', CellBox)
Vue.component('cell', Cell)
Vue.component('group', Group)
Vue.component('x-input', XInput)
Vue.component('popup', Popup)
Vue.component('load-more', LoadMore)
Vue.component('button-tab', ButtonTab)
Vue.component('button-tab-item', ButtonTabItem)

router.beforeEach((to, from, next) => {
let title = '弘真艺拍'
title = to.meta && to.meta.title ? to.meta.title + ' - ' + title : title;
document.title = title;

if (store.state.token) {
next()
} else {
if (to.query.token) {
store.commit('addToken', to.query.token);
next()
} else {
wx.miniProgram.navigateTo({ url: '/pages/login/login?return_url=' + decodeURIComponent(to.fullPath) })
}
}
})


Vue.prototype.$http.interceptors.request.use(
config => {
if (store.state.token) { // 判断是否存在token，如果存在的话，则每个http header都加上token。废弃cookie

config.headers.Authorization = `${store.state.token}`;
}
return config;
},
err => {
return Promise.reject(err);
});
Vue.prototype.$http.interceptors.response.use(
response => {
if (response.data.errno == 4002) { //未登录为4002,4002时清除token，跳转到登录页
store.commit('addToken', '');
wx.miniProgram.navigateTo({ url: '/pages/login/login?return_url=' + encodeURIComponent(router.currentRoute.fullPath) })
}
return response
},
error => {
return Promise.reject(error)
});


FastClick.attach(document.body)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router,
store,
render: h => h(App)
}).$mount('#app-box')



