
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

export default {
data() {
return {
show: false,
isLoading: true,
hasContent: false,
goods: [],
page: 0,
fetching: true,
noMore: false,
fee: 0,
pay_price: 0,
order_number: '',
addAddress: true,
payBtn: false
}
},
created() {
this.getAddress()
this.createdDate()
},
mounted() {
window.addEventListener("scroll", this.handleScroll);
},
methods: {
getAddress() {
let that = this
if (Object.keys(that.$store.state.defaultAddress).length == 0) {
this.$http.get('/api/address')
.then(function(res) {
if (res.data.data.length) {
that.$store.commit('setAddress', res.data.data[0])
} else {
that.addAddress = false
that.payBtn = true
}
})
.catch(function(error) {
console.log(error);
});
}
},
createdDate() {
this.page = 0;
this.fetching = true;
this.noMore = false;
document.documentElement.scrollTop = 0;
document.body.scrollTop = 0;
this.isLoading = true
this.$http.get('/api/waitingPay?page=' + this.page)
.then((res) => {
this.goods = res.data.data
this.isLoading = false
if (this.goods.length) {
this.hasContent = true
}
})
.catch((err) => {
console.log(err)
})
},
handleScroll() {
let that = this
let heightTop = document.documentElement.scrollTop || document.body.scrollTop;

if (heightTop + window.innerHeight >= document.body.offsetHeight) {

if (this.fetching == true) {

this.fetching = false;
this.page++
this.$http.get('/api/waitingPay?page=' + this.page)
.then(function(res) {
if (res.data.data !== null) {
that.goods = that.goods.concat(res.data.data)

that.fetching = true;
} else {
that.noMore = true
}
})
.catch(function(error) {
console.log(error);
});
}
}
},
addressInfo(data) {
console.log(data.order_number)
this.show = true
this.order_number = data.order_number
this.fee = data.fee
this.pay_price = data.pay_price
},
pay() {
let that = this
const params = new URLSearchParams();
console.log(parseInt(this.order_number))
params.append('order_number', parseInt(this.order_number));
params.append('address_id', this.addressData.id);
this.$http.post('/api/address', params)
.then(function(res) {
if (res.data.errno == 1000) {
wx.miniProgram.navigateTo({ url: '/pages/pay/pay?order_number=' + that.order_number + '&return_url=' + encodeURIComponent(location.protocol + '//' + location.host + '/promise_success/' + that.order_number) })
}
})
}
},
computed: {
sumPrice() {
return this.fee + this.pay_price
},
addressData() {
return this.$store.state.defaultAddress
}
},
components: {
Selector,
PopupPicker,
XAddress,
},
beforeDestroy() {
window.removeEventListener("scroll", this.handleScroll);
}
}




