
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

export default {
data() {
return {
addressData: ChinaAddressV4Data,
name: '',
telephone: '',
addressValue: [],
address: '',
toastValue: false,
msg: '',
}
},
beforeCreate() {},
created() {},
mounted() {},
methods: {
postAddress() {
let that = this
const params = new URLSearchParams();
params.append('name', this.name);
params.append('telephone', this.telephone);
params.append('address', this.address);
params.append('province_id', this.addressValue[0]);
params.append('city_id', this.addressValue[1]);
params.append('area_id', this.addressValue[2]);
this.$http.post('/api/address', params)
.then((res) => {
this.toastValue = true;
if(res.data.errno == 1000){
this.msg = '保存成功'
setTimeout(function(){
that.$router.go(-1)
},1000);
}else{
this.msg = res.data.message
}
})
.catch((err) => {
this.msg = err;
})
}
},
computed: {},
components: {
Selector,
PopupPicker,
XAddress,
}
}




