
import Msg from 'vux/src/components/msg/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
data() {
return {
icon: 'success',
buttons: [{
type: 'primary',
text: '继续出价',
onClick: this.updateOrder
}, {
type: 'default',
text: '返回商品',
link: '/demo'
}]
}
},
beforeCreate() {
},
created() {},
mounted() {
},
methods: {
updateOrder(){
var token = window.$cookies.get('token')  || ''
var params = new URLSearchParams();
params.append('order_number', this.$route.params.id);       
params.append('status', 1);

this.$http.post('/pay/updateOrder',params).then((res)=>{
console.log(res)

})
}
},
computed: {
},
components: {
Msg,
XButton
}
}




