
import LoadMore from 'vux/src/components/load-more/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

import tabBar from '../conponent/tabbar.vue'

export default {
data() {
return {
isLoading: true,
hasContent: false,
state: ['0', '1', '2'], //进行中，预拍，已结束
goods: [],
stateActive: '',
page: 0,
fetching: true,
noMore: false,
offerColor: ['red', 'green'],
payColor: ['red', 'green'],
offerStatus: ['已出局', '暂领先'],
payStatus: ['未付款', '已付款']
}
},
created() {
this.createdDate(0)
},
mounted() {
window.addEventListener("scroll", this.handleScroll);
},
methods: {
createdDate(index) {
this.page = 0;
this.fetching = true;
this.noMore = false;
document.documentElement.scrollTop = 0;
document.body.scrollTop = 0;
this.isLoading = true
this.stateActive = this.state[index]
this.$http.get('/api/myPai?status=' + this.stateActive + '&page=' + this.page)
.then((res) => {
this.goods = res.data.data
this.isLoading = false
if (this.goods.length) {
this.hasContent = true
} else {
this.hasContent = false
}
})
.catch((err) => {
console.log(err)
})
},
handleScroll() {
let that = this
let heightTop = document.documentElement.scrollTop || document.body.scrollTop;

if (heightTop + window.innerHeight >= document.body.offsetHeight) {

if (this.fetching == true) {

this.fetching = false;
this.page++
this.$http.get('/api/getGoodList?status=' + this.stateActive + '&page=' + this.page)
.then(function(res) {
if (res.data.data.length > 0) {
that.goods = that.goods.concat(res.data.data)

that.fetching = true;
} else {
that.noMore = true
}
})
.catch(function(error) {
console.log(error);
});
}
}
},
offer_status(data) {
return this.offerStatus[data]
},
offer_Color(data) {
return this.offerColor[data]
},
pay_status(data) {
return this.payStatus[data]
},
pay_Color(data) {
return this.payColor[data]
},
},
computed: {

},
components: {
Tab,
TabItem,
LoadMore,
ButtonTab,
ButtonTabItem
},
beforeDestroy() {
window.removeEventListener("scroll", this.handleScroll);
}
}




