
import Card from 'vux/src/components/card/index.vue'

export default {
data() {
return {
list: [],
page: 0
}
},
beforeCreate() {},
created() {
this.creatInfo()
},
mounted() {

},
methods: {
creatInfo() {
this.$http.get('/api/getNewsList' + '?page=' + this.page)
.then((res) => {
this.list = res.data.data
})
.catch((err) => {
console.log(err)
})
}
},
computed: {},
components: {
Card
}
}




