
import Card from 'vux/src/components/card/index.vue'

export default {
data() {
return {
title: '',
content: '',
time: '',
source: ''
}
},
beforeCreate() {},
created() {
this.creatInfo()
},
mounted() {},
methods: {
creatInfo() {
this.$http.get('/api/getNewsInfo?id=' + this.$route.params.id)
.then((res) => {
this.title = res.data.data.title
this.content = res.data.data.content
this.time = res.data.data.create_time_fmt
this.source = res.data.data.source
})
.catch((err) => {
console.log(err)
})
}
},
computed: {},
components: {
Card
}
}




