
import Blur from 'vux/src/components/blur/index.vue'

export default {
data() {
return {
headPic: 'static/img/head_pic.jpg',
nickname:'弘真艺拍'
}
},
beforeCreate() {
this.$http.get('/api/userinfo')
.then((res) => {
if (res.data.data) {
this.headPic = res.data.data.pic
this.nickname = res.data.data.nick
}
})
.catch((err) => {
console.log(err)
})
},

methods: {},
computed: {

},
components: {
Blur
}
}




